<template>
  <div>
    <div class="bedPage">
      <head-top head-title="患者详情" go-back='true'></head-top>
      <section class="bed_name">
        <div class="profileImage">
          <img v-if="bedDetailData.Gender == 0" src="../assets/icon/woman.png" >
          <img v-else src="../assets/icon/man.png" >
          <div class="user-info">
            <header>
              <h4 class="bed_title ellipsis" :class="'lv_'+ bedDetailData.NursingLevel">{{bedDetailData.Name}}</h4>
              <ul>
                <li>病例号:{{bedDetailData.ChartNo}}</li>
              </ul>
            </header>
            <h5 class="bed_distance">
              <p>{{bedDetailData.UID}}</p>
              <p>{{bedDetailData.BedNum}}号床</p>
            </h5>
            <p>{{bedDetailData.ChkInAt}} 入院</p>
          </div>
        </div>
        <div class="alink_r">
          <router-link :to="{path: 'bedAdv', query:{id}}">查看医嘱</router-link>
          <router-link :to="{path: 'medication', query:{id}}">用药详情</router-link>
        </div>
      </section>
      <section class="tab_title">
        <span :class="{choosed: categoryType === 1}" @click="categoryType = 1">基本信息</span>
        <span :class="{choosed: categoryType === 2}" @click="categoryType = 2">特殊标记</span>
        <span :class="{choosed: categoryType === 3}" @click="categoryType = 3">检查结果</span>
      </section>
      <transition name="router-fade">
        <section v-if="categoryType === 1">
          <dl class="bedBest_container">
              <dd class="bedBest_list">
                <dl>
                  <dd>主治医生：<em>{{bedDetailData.Doctor}}</em></dd>
                  <dd>责任护士：<em>{{bedDetailData.ResNurse}}</em></dd>
                  <dd>住院天数： <em>{{bedDetailData.DOH}}</em></dd>
                  <dd>缴费方式：<em>{{bedDetailData.Insurance}}</em></dd>
                </dl>
              </dd>
              <dd class="bedBest_list">
                <dl>
                  <dd>护理级别：<em>{{bedDetailData.NursingLevel}}</em></dd>
                  <dd>紧急联系人：<em>{{bedDetailData.Contact}}</em></dd>
                  <dd>联络人电话：<em>{{bedDetailData.ContactNo}}</em></dd>
                  <dd>今办明出：<em v-if="bedDetailData.WBD ==1">是</em><em v-else>否</em></dd>
                  <dd>设备需求：<em v-if="bedDetailData.Equipment == 0">无</em><em v-else>用氧</em></dd>
                  <dd>费用：<em>{{bedDetailData.Fee}}</em></dd>
                </dl>
              </dd>
            <dd class="bedBest_list">
              <dl>
                <dd>危急值：<em>{{bedDetailData.Critical}}</em></dd>
                <dd>导管评估：<em>{{bedDetailData.CatheterOff}}</em></dd>
                <dd>跌倒评估：<em>{{bedDetailData.FallAssess}}</em></dd>
                <dd>压疮评估：<em>{{bedDetailData.SoreAssess}}</em></dd>
                <dd>疾病严重度：<em>{{bedDetailData.DesSev}}</em></dd>
                <dd v-if="bedDetailData.Delivery != 0">分娩情况：
                  <em v-if="bedDetailData.Delivery == 1">男婴</em>
                  <em v-if="bedDetailData.Delivery == 2">女婴</em>
                  <em v-if="bedDetailData.Delivery == 3">双胞胎</em>
                  <em v-if="bedDetailData.Delivery == 4">三胞胎及以上</em>
                </dd>
              </dl>
            </dd>
          </dl>
        </section>
      </transition>
      <transition name="router-fade">
        <section v-if="categoryType === 2">
          <dl class="bedTag_container">
            <dd class="bed_Switch">
              <dl>
                <dd>病重 <em><toggle-button v-if="bedDetailData.Critically == 1" :labels="true" :value="true" />
                  <toggle-button v-else :labels="true"/>
                </em>
                </dd>
                <dd>病危 <em><toggle-button v-if="bedDetailData.Critically == 2" :labels="true" :value="true" />
                  <toggle-button v-else :labels="true"/></em></dd>
                <dd>绝对卧床休息/床上活动 <em><toggle-button v-if="bedDetailData.BedRest == 1" :labels="true" :value="true" />
                  <toggle-button v-else :labels="true"/></em></dd>
                <dd>特殊疾病 <em><toggle-button v-if="bedDetailData.SDC == 1" :labels="true" :value="true" />
                  <toggle-button v-else :labels="true"/></em></dd>
              </dl>
            </dd>
            <dd class="bed_Switch">
              <dl>
                <dd>DVT高风险 <em><toggle-button v-if="bedDetailData.DVTRisk == 1" :labels="true" :value="true" />
                  <toggle-button v-else :labels="true"/></em></dd>
                <dd>特殊饮食 <em><toggle-button v-if="bedDetailData.SpecialDiets == 1" :labels="true" :value="true" />
                  <toggle-button v-else :labels="true"/></em></dd>
                <dd>禁食 <em><toggle-button v-if="bedDetailData.Fasting == 1" :labels="true" :value="true" />
                  <toggle-button v-else :labels="true"/></em></dd>
                <dd>保护性约束 <em><toggle-button v-if="bedDetailData.Constraint == 1" :labels="true" :value="true" />
                  <toggle-button v-else :labels="true"/></em></dd>
                <dd>输血 <em><toggle-button v-if="bedDetailData.Transfuse == 1" :labels="true" :value="true" />
                  <toggle-button v-else :labels="true"/></em></dd>
              </dl>
            </dd>
            <dd class="bed_Switch">
              <dl>
                <dd>跌倒/坠床高风险 <em><toggle-button v-if="bedDetailData.hrf == 1" :labels="true" :value="true" />
                  <toggle-button v-else :labels="true"/></em></dd>
                <dd>防导管滑脱 <em><toggle-button v-if="bedDetailData.CatheterOff == 1" :labels="true" :value="true" />
                  <toggle-button v-else :labels="true"/></em></dd>
                <dd>压疮高风险 <em><toggle-button v-if="bedDetailData.SoreRisk == 1" :labels="true" :value="true" />
                  <toggle-button v-else :labels="true"/></em></dd>
                <dd>院内感染 <em><toggle-button v-if="bedDetailData.InfecInside == 1" :labels="true" :value="true" />
                  <toggle-button v-else :labels="true"/></em></dd>
                <dd>过敏 <em><toggle-button v-if="bedDetailData.Allergy == 1" :labels="true" :value="true" />
                  <toggle-button v-else :labels="true"/></em></dd>
                <dd>隔离 <em><toggle-button v-if="bedDetailData.Isolation == 1" :labels="true" :value="true" />
                  <toggle-button v-else :labels="true"/></em></dd>
              </dl>
            </dd>
          </dl>
        </section>
      </transition>
      <transition name="router-fade">
        <section v-if="categoryType === 3">
          <section class="bedClinic_container">
            {{bedDetailData.bedcontainer}}
          </section>
        </section>
      </transition>
    </div>
  </div>
</template>

<script>
  import {bedDetails} from '../service/getData'
  import headTop from '../components/head'
  import ToggleButton from '../components/switch.vue'

  export default {
    data(){
      return {
        categoryType: 1,
        id: null,
        bedDetailData: [],
        toggled: false,
      }
    },
    created(){
      this.id = this.$route.query.id;
    },
    mounted(){
      this.initData();
    },
    components:{
      headTop,
      ToggleButton
    },
    methods: {
      async initData() {
        this.bedDetailData = await bedDetails(this.id)
      },
    },
  }
</script>
<style>
  .bed_name {
    background:#fff;
    padding-top: 1.95rem;
  }
  .bed_name .profileImage {
    display: block;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    box-align: center;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    padding:.6rem .6rem 0 .6rem;
  }
  .bed_name .profileImage img {
    background:#eee;
    display: inline-block;
    width: 2.7rem;
    height: 2.7rem;
    border-radius: 50%;
    vertical-align: middle;
  }
  .bed_name .profileImage .user-info {
    padding-left: .4rem;
    -webkit-box-flex: 1;
    -webkit-flex-grow: 1;
    -ms-flex-positive: 1;
    flex-grow: 1;
    -webkit-box-flex: 1;
    -ms-flex: auto;
    flex: auto;
  }
  .bed_name .profileImage .user-info header {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: justify;
    -ms-flex-pack: justify;
    justify-content: space-between;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
  }
  .bed_name .profileImage .user-info .bed_title {
    display: -webkit-box;
    display: -webkit-flex;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
    -webkit-align-items: center;
    -ms-flex-align: center;
    align-items: center;
    margin: 0;
    width: 4.5rem;
    color: #333;
    padding-top: .01rem;
    font-size: .65rem;
    line-height: 1rem;
    font-weight: 700;
  }
  .bed_name .profileImage .user-info header ul {
    display: flex;
  }
  .bed_name .profileImage .user-info header ul li {
    padding: 0 .2rem;
    font-size: .55rem;
  }
  .bed_name .user-info .bed_distance {
    display: flex;
    width: 100%;
    font-size:.55rem;
    line-height:.75rem;
    margin-top:.2rem;
  }
  .bed_name .profileImage .user-info p {
    margin-top:-.4rem;
  }
  .bed_name .user-info .bed_distance p:first-child {
    margin-top:0;
    float:left;
    width:60%;
  }
  .bed_name .user-info .bed_distance p:last-child {
    margin-top:0;
    float:left;
    color:#f0665a;
    line-height: 1.2rem;
    font-size:.65rem;
  }
  .tab_title {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: distribute;
    justify-content: space-around;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    background-color: #fff;
    height: 2rem;
    border-top:1px solid #e7eaf1;
    border-bottom: 1px solid #e7eaf1;
    margin-bottom: 1rem;
  }
  .tab_title span {
    border-right:1px solid #eee;
    height: 2rem;
    width: 33.333%;
    text-align: center;
    line-height: 2rem;
    font-size: .6rem;
  }
  .tab_title span.choosed {
    background-color:#47a8f0;
    color: #fff;
  }
  .tab_title span:last-child {
    border-right:none;
  }
  .bedClinic_container {
    margin:1rem 0;
    padding:.5rem;
    background-color:#fff;
    line-height:25px;
    font-size:14px;
    min-height:18rem;
    border-top:1px solid #eee;
    border-bottom:1px solid #eee;
  }
  dl.bedBest_container,dl.bedTag_container {
    margin-bottom:.1rem;
  }
  dl.bedBest_container .bedBest_list,dl.bedTag_container .bed_Switch {
    margin-bottom:1rem;
    background-color: #fff;
  }
  dl.bedBest_container .bedBest_list dl,.bedTag_container .bed_Switch dl {
    border-top:1px solid #eee;
  }
  dl.bedBest_container .bedBest_list dd,.bedTag_container .bed_Switch dd {
    position:relative;
    border-bottom:1px solid #eee;
    font-size:.6rem;
    padding:.4rem 1rem .4rem .5rem;
  }
  dl.bedBest_container .bedBest_list dd em,.bedTag_container .bed_Switch dd em {
    float: right;
    font-size: .55rem;
    color: #999;
    font-style: normal;
    font-weight: 400;
  }
  .alink_r {
    display: flex;
    clear: both;
    padding:0 0 .5rem 60%;
  }
  .alink_r a {
    float: right;
    display: inline-block;
    background-color: #47a7f0;
    color:#fff;
    font-size:.5rem;
    line-height: .7rem;
    padding:.1rem .3rem;
    margin-left:.35rem;
    border-radius:.25rem;
  }
</style>



